import React, { useEffect, useState, useRef } from "react";
import { useSelector, useDispatch } from 'react-redux'
import Badge from '@mui/material/Badge';
import NotificationsIcon from '@mui/icons-material/Notifications';

import ChatBtn from "../ChatBtn";
import Notification from "../Sidemenu/Notification";

import styles from './SmallMenuBtn.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

import agent from '../../api'

import {
	chatting,
	showToast,
	showChat,
	changePage
} from '../../actions/common'

import { logout } from '../../actions/auth'
import { common } from "@mui/material/colors";

const SmallMenuBtn = () => {
	const dispatch = useDispatch()

	const auth = useSelector(state => state.auth)
	const common = useSelector(state => state.common)
	const showChatBtn = useSelector(state => state.common.showChat)
	const isChat = useSelector(state => state.common.chatting)

	const [showMenu, setShowMenu] = useState(false)
	const [showNotification, setShowNotification] = useState(false)
	const [hoverNum, setHoverNum] = useState(0)
	const [notification, setNotification] = useState(0);
	const [notificationData, setNotificationData] = useState([]);


	useEffect(() => {
		const getUserData = async () => {
			try {
				const resNotificationData = await agent.common.getNotification()
				if (resNotificationData.data.success) {
					let notificationArray = []
					resNotificationData.data.data.map((notUser) => {
						notificationArray.push({
							id: notUser.id,
							title: notUser.title,
							description: notUser.message,
							type: notUser.type,
							link: "",
							viewed: notUser.read,
						})
					})

					setNotificationData([...notificationArray])
					let count = 0;
					notificationArray.map((alarm) => {
						if (!alarm.viewed) {
							count++;
						}
					})
					setNotification(count)
				}
			} catch (error) {
				if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
					dispatch(showToast('error', error.response.data.message))
					if (error.response.data.message == 'Unauthorized') {
						localStorage.removeItem('token')
						dispatch(logout())
						window.dragon.stopUnityGame()
						dispatch(changePage('login'))
					}
					// clearInterval(intervalID);
					// console.log("interval id: ", intervalID)
					// intervalID = -1
				}
			}
		}

		const getAdminData = async () => {
			try {
				const resNotificationData = await agent.common.notification()
				const resNotificationUserData = await agent.common.notificationUser()
				if (resNotificationData.data.success && resNotificationUserData.data.success) {
					let notificationArray = []
					resNotificationUserData.data.data.map((notUser) => {
						if(notUser.id_user === auth.currentUser.id){
							for(let i=0; i<resNotificationData.data.data.length; i++) {
								if(notUser.id_notification === resNotificationData.data.data[i].id) {
									let type
									switch(resNotificationData.data.data[i].type) {
										case "LESSON":
											type = 1
											break
										case "MEETING":
											type = 2
											break
										case "INTERACTION":
											type = 3
											break
									}
									notificationArray.push({
										id: notUser.id_notification,
										title: resNotificationData.data.data[i].title,
										description: resNotificationData.data.data[i].description,
										type: type,
										link: resNotificationData.data.data[i].link,
										viewed: notUser.viewed,
									})
								}
							}
						}
					})

					setNotificationData([...notificationArray])
					let count = 0;
					notificationArray.map((alarm) => {
						if (!alarm.viewed) {
							count++;
						}
					})
					setNotification(count)
				}
			} catch (error) {
				if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
					dispatch(showToast('error', error.response.data.message))
					if (error.response.data.message == 'Unauthorized') {
						localStorage.removeItem('token')
						dispatch(logout())
						window.dragon.stopUnityGame()
						dispatch(changePage('login'))
					}
					// clearInterval(intervalID);
					// console.log("interval id: ", intervalID)
					// intervalID = -1
				}
			}
		}

		if(auth.currentUser.token !== '') {
			if(auth.currentUser.role !== 'INTERPRETER' && auth.currentUser.role !== 'SPEAKER') {
				if(auth.currentUser.role === 'ADMIN') {
					getAdminData()
				} else {
					getUserData()
				}
			}
		}

		// intervalID = setInterval(function () {
		// 	getData()
		// 	clearInterval(intervalID);
		// }, 10000);
	}, [auth.currentUser.token])

	const toggleNotification = () => {
		setShowNotification(old => !old)
	}

	const startChat = () => {
		dispatch(chatting(true))
		dispatch(showChat(true))
	}

	const handleClickPlay = () => {
    dispatch(changePage('play'))
    window.dragon.makeCharacter(auth.characterData)
    window.dragon.clickPlay(auth.currentUser.role, common.nextEvent, auth.currentUser.id)
  }

	return (
		<>
			{
				!showChatBtn && common.page !== 'login' && common.page !== 'none' &&
				<div className={styles.small_menu}>
					<div className={styles.rel_container}>
						{
							showMenu ? 
								<>
									<div className={styles.small_menu_btn} onClick={() => setShowMenu(false)}>
										<img src={getImg('image/icons/allbtn_open.png')} alt="Logo" />
									</div>
									<div className={styles.small_menu_container}>
										<div className={styles.rel_container}>
											<div className={styles.menu_header}>
												<div className={styles.text}>
													Atalhos
												</div>
												<div className={styles.header_border}></div>
											</div>
											<div className={styles.menu_content}>
												<div className={styles.menu_item_container}>
													<div className={`${styles.menu_item_main} ${hoverNum === 1 && styles.hover}`} onClick={() => handleClickPlay()} onMouseOver={() => setHoverNum(1)} onMouseLeave={() => setHoverNum(0)}>
														<img src={getImg('image/icons/Universe.png')} alt="Logo" />
														<div className={styles.text}>
															Universo
														</div>
													</div>
													{
														hoverNum === 1 && <div className={styles.menu_item_pro}></div>
													}
												</div>
												{
													auth.currentUser.role !== 'INTERPRETER' && auth.currentUser.role !== 'SPEAKER' &&
													<div className={styles.menu_item_container}>
														<div className={`${styles.menu_item_main} ${hoverNum === 2 && styles.hover}`} onClick={() => setShowNotification(old => !old)} onMouseOver={() => setHoverNum(2)} onMouseLeave={() => setHoverNum(0)}>
															<Badge className={styles.badge} badgeContent={notification}>
																<NotificationsIcon />
															</Badge>
															<div className={styles.text}>
																Notificações
															</div>
														</div>
														{
															hoverNum === 2 && <div className={styles.menu_item_pro}></div>
														}
													</div>
												}
												{
													auth.currentUser.role !== 'INTERPRETER' && auth.currentUser.role !== 'SPEAKER' &&
														<div className={styles.menu_item_container}>
															<div className={`${styles.menu_item_main} ${hoverNum === 3 && styles.hover}`} onClick={() => startChat()} onMouseOver={() => setHoverNum(3)} onMouseLeave={() => setHoverNum(0)}>
																<img src={getImg('image/icons/small_menu_chat.png')} alt="Logo" />
																<div className={styles.text}>
																	Chat
																</div>
															</div>
															{
																hoverNum === 3 && <div className={styles.menu_item_pro}></div>
															}
														</div>
												}
											</div>
										</div>
									</div>
									{
										showNotification &&
										<div className={styles.notification_container}>
											<div className={styles.rel_container}>
												<Notification  alarms={notificationData} toggleNotification={toggleNotification} />
											</div>
										</div>
									}
								</>
								:
								<div className={styles.small_menu_btn} onClick={() => setShowMenu(true)}>
									<img src={getImg('image/icons/allbtn_close.png')} alt="Logo" />
								</div>
						}
					</div>
				</div>
			}
			{
				auth.currentUser.role !== 'INTERPRETER' && auth.currentUser.role !== 'SPEAKER' && common.page !== 'login' && common.page !== 'none' && <ChatBtn />
			}
		</>
	)
}

export default SmallMenuBtn