import React, { useEffect, useRef, useState } from "react";
import CloseIcon from '@mui/icons-material/Close';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';

import styles from './Controls.module.scss';
import {getImg} from '../../../utils/Helper'

let intervalId1
let intervalId2
let time = 8000

const Controls = (props) => {

  const [stepNum, setStepNum] = useState(1)
  const [aniNum, setAniNum] = useState(1)
  const [showSmall, setShowSmall] = useState(false)

  const handleClick = (num) => {
    if(stepNum >= num) {
      setStepNum(num)
    }
  }

  const showBigImg = () => {
    setTimeout(() => {
      setShowSmall(old => !old);
      if(stepNum === 3) {
        showSmallimg()
      }
    }, 8000);
  }

  const showSmallimg = () => {
    setTimeout(() => {
      setShowSmall(old => !old);
      if(stepNum === 3) {
        showBigImg()
      }
    }, 1000);
  }

  useEffect(() => {
    if(stepNum === 3) {
      intervalId1 = setInterval(() => {
        setAniNum(old => old >= 3 ? 1 : old + 1);
      }, 250)
      showBigImg()

    } else {
      clearInterval(intervalId1)
    }
  }, [stepNum])

	return (
		<div className={styles.full_container}>
      <div className={styles.rel_container}>
        <div className={styles.card_container}>
          <div className={styles.main_card_container}>
            <div className={styles.close_btn} onClick={() => props.setShowControlModal(false)}>
              <CloseIcon sx={{ fontSize: 15, color: '#000' }} />
            </div>
            {
              stepNum === 1 &&
                <div className={styles.step1_content}>
                  <div className={styles.text}>
                    Use os botões W, A, S, D ou as setas para controlar o avatar. 
                  </div>
                  <div className={styles.content_container}>
                    <div className={styles.left_content}>
                      <div className={styles.left_btn}>
                        <ChevronLeftIcon sx={{ fontSize: 28, color: '#FFF' }}/>
                      </div>
                      <div className={styles.down_btn}>
                        <KeyboardArrowDownIcon sx={{ fontSize: 28, color: '#FFF' }}/>
                      </div>
                      <div className={styles.right_btn}>
                        <ChevronRightIcon sx={{ fontSize: 28, color: '#FFF' }}/>
                      </div>
                      <div className={styles.top_btn}>
                        <KeyboardArrowUpIcon sx={{ fontSize: 28, color: '#FFF' }}/>
                      </div>
                    </div>
                    <div className={styles.middle_content}>
                      ou
                    </div>
                    <div className={styles.right_content}>
                      <div className={styles.left_btn}>
                        A
                      </div>
                      <div className={styles.down_btn}>
                        S
                      </div>
                      <div className={styles.right_btn}>
                        D
                      </div>
                      <div className={styles.top_btn}>
                        W
                      </div>
                    </div>
                  </div>
                </div>
            }

            {
              stepNum === 2 &&
                <div className={styles.step2_content}>
                  <div className={styles.text}>
                    Segure SHIFT com outra tecla para o personagem correr na direção desejada.
                  </div>
                  <div className={styles.content_container}>
                    <div className={styles.left_content}>shift</div>
                    <div className={styles.middle_content}>+</div>
                    <div className={styles.right_content}>
                      <div className={styles.btn}>
                        <KeyboardArrowUpIcon sx={{ fontSize: 45, color: '#FFF' }}/>
                      </div>
                    </div>
                  </div>
                </div>
            }
            {
              stepNum === 3 && 
                <div className={styles.step3_content}>
                  <div className={styles.text}>
                    Ficou com alguma dúvida?
                    <br />
                    Aperte “F” para abrir a central de dicas.
                  </div>
                  <div className={styles.content_container}>
                    <div className={styles.left_content}>
                      <div className={styles.btn}>
                        F
                      </div>
                    </div>
                    <div className={styles.middle_content}>
                      {aniNum === 1 && <img src={getImg('image/icons/become1.png')} alt="Logo" />}
                      {aniNum === 2 && <img src={getImg('image/icons/become2.png')} alt="Logo" />}
                      {aniNum === 3 && <img src={getImg('image/icons/become3.png')} alt="Logo" />}
                    </div>
                    <div className={styles.right_content}>
                      {
                        showSmall ?
                          <img className={styles.small} src={getImg('image/icons/hint_small.png')} alt="Logo" />
                          :
                          <img className={styles.big} src={getImg('image/icons/hint_big.png')} alt="Logo" />
                      }
                      
                    </div>
                  </div>
                </div>
            }
            {
              stepNum === 4 &&
                <div className={styles.step4_content}>
                  <div className={styles.first_text}>
                    Use o mouse para rotacionar a câmera.
                  </div>
                  <div className={styles.first_image}>
                    <img className={styles.camera} src={getImg('image/icons/camera.png')} alt="Logo" />
                    <img className={styles.loading} src={getImg('image/icons/camera_loading.png')} alt="Logo" />
                  </div>
                  <div className={styles.second_text}>
                    Use o “scroll” para interagir com outros avatares.
                  </div>
                  <div className={styles.second_image}>
                    <img className={styles.mouse} src={getImg('image/icons/mouse.png')} alt="Logo" />
                    <img className={styles.scrolling} src={getImg('image/icons/mouse_scrolling.png')} alt="Logo" />
                  </div>
                </div>
            }
            
          </div>
          <div className={styles.step_card_container}>
            <div className={styles.step_main}>
              <div className={styles.bars}>
                <div className={`${stepNum > 1 ? styles.stepbar : styles.stepBar_disable}`}></div>
                <div className={`${stepNum > 2 ? styles.stepbar : styles.stepBar_disable}`}></div>
                <div className={`${stepNum > 3 ? styles.stepbar : styles.stepBar_disable}`}></div>
              </div>
              <div className={styles.steps}>
                <div className={`${stepNum >= 1 ? styles.step : styles.step_disable}`} onClick={() => handleClick(1)}>1</div>
                <div className={`${stepNum >= 2 ? styles.step : styles.step_disable}`} onClick={() => handleClick(2)}>2</div>
                <div className={`${stepNum >= 3 ? styles.step : styles.step_disable}`} onClick={() => handleClick(3)}>3</div>
                <div className={`${stepNum >= 4 ? styles.step : styles.step_disable}`} onClick={() => handleClick(4)}>4</div>
              </div>
            </div>
            {
              stepNum > 1 &&
                <div className={styles.left_btn} onClick={() => setStepNum((old) => old - 1)}>
                  <ChevronLeftIcon sx={{ fontSize: 25, color: '#FFF' }} />
                </div>
            }
            {
              stepNum < 4 &&
                <div className={styles.right_btn} onClick={() => setStepNum((old) => old + 1)}>
                  <ChevronRightIcon sx={{ fontSize: 25, color: '#FFF' }} />
                </div>
            }
          </div>
        </div>
      </div>
    </div>
	);
}

export default Controls;
