import {
  CHANGE_PAGE,
  CHANGE_PAGE_ID,
  START_GAME,
  START_ACTION,
  END_ACTION,
  SHOW_TOAST,
  HIDE_TOAST,
  SHOW_CHAT,
  CHATTING,
  SHOW_SMALL,
  IS_LIVE,
  ADD_TYPE,
  ADD_NEXT_EVENT
} from '../constants/actionTypes/common'

export const changePage = (page) => ({ type: CHANGE_PAGE, page })

export const changePageId = (pageId) => ({ type: CHANGE_PAGE_ID, pageId })

export const startGame = () => ({ type: START_GAME })

export const startAction = () => ({ type: START_ACTION })

export const endAction = () => ({ type: END_ACTION })

export const showToast = (msgType, content) => ({ type: SHOW_TOAST, msgType, content })

export const hideToast = () => ({ type: HIDE_TOAST })

export const showChat = (isShow) => ({ type: SHOW_CHAT, isShow })

export const chatting = (isChat) => ({ type: CHATTING, isChat })

export const isLive = (isLive) => ({ type: IS_LIVE, isLive })

export const showSmall = (isSmall) => ({ type: SHOW_SMALL, isSmall })

export const addType = (typeList) => ({ type: ADD_TYPE, typeList })

export const addNextEvent = (nextEvent) => ({ type: ADD_NEXT_EVENT, nextEvent })