import React, { useEffect, useRef, useState } from "react";
import CloseIcon from '@mui/icons-material/Close';

import styles from './Tip.module.scss';
import {getImg} from '../../../utils/Helper'


const Tip = (props) => {

	return (
		<div className={styles.full_container}>
      <div className={styles.rel_container}>
        <div className={styles.card_container}>
          <div className={styles.close_btn} onClick={() => props.setShowTipModal(false)}>
            <CloseIcon sx={{ fontSize: 15, color: '#000' }} />
          </div>
          <div className={styles.main_content}>
            <div className={styles.logo_container}>
              <img src={getImg('image/icons/tip.png')} alt="icons" />
            </div>
            <div className={styles.tip_title}>
              Buscando Stickers
            </div>
            <div className={styles.tip_content}>
              Em cada sessão do centro de convenções você encontrará stickers diferentes para preencher a sua cartela e conquistar o grande prêmio.
              <br/>
              <br/>
              Mas não esqueça de explorar todas as áreas.
            </div>
          </div>
        </div>
      </div>
    </div>
	);
}

export default Tip;
