import React, { useEffect, useState, useRef } from "react"
import { useSelector, useDispatch } from 'react-redux'
import Badge from '@mui/material/Badge';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import VolumeMuteIcon from '@mui/icons-material/VolumeMute';
import MicIcon from '@mui/icons-material/Mic';
import MicOffIcon from '@mui/icons-material/MicOff';
import LockIcon from '@mui/icons-material/Lock';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { faLock } from "@fortawesome/fontawesome-free-solid";
import { faUnlock } from "@fortawesome/fontawesome-free-solid";
import { faDoorOpen } from "@fortawesome/fontawesome-free-solid";
import { faExpand } from "@fortawesome/fontawesome-free-solid";
import { faCompress } from "@fortawesome/fontawesome-free-solid";

import Notification from '../../components/Sidemenu/Notification'
import Tip from "../../components/Tutorial/Tip";
import Controls from "../../components/Tutorial/Controls";

import styles from './Play.module.scss'
import { getImg, useWindowSize } from "../../utils/Helper"


import agent from '../../api/'

import {
  changePage,
  startAction,
  endAction,
  showToast,
  isLive
} from '../../actions/common'
import { logout, createCharacter } from '../../actions/auth'



const Play = () => {

  const dispatch = useDispatch()
  const auth = useSelector(state => state.auth)
  const common = useSelector(state => state.common)

  const [windowWidth, windowHeight] = useWindowSize()

  const [mapData, setMapData] = useState([])
  const [showNotification, setShowNotification] = useState(false)
  const [notification, setNotification] = useState(0)
  const [notificationData, setNotificationData] = useState([])
  const [showBtn, setShowBtn] = useState(false)
  const [showFullScreenBtn, setShowFullScreenBtn] = useState(false)
  const [lockCamera, setLockCamera] = useState(true)
  const [avatarImg, setAvatarImg] = useState('')
  const [detailMiddleBtn, setDetailMiddleBtn] = useState(false)
  const [showTipModal, setShowTipModal] = useState(false)
  const [showControlModal, setShowControlModal] = useState(false)
  const [activeSound, setActiveSound] = useState(true)
  const [aciveMic, setActiveMic] = useState(true)

  const handleClick = () => {
    // window.dragon.clickMap(mapData)
    // window.dragon.unityKeyboard()
  }

  const toggleNotification = () => {
    setShowNotification((old) => old ? false : true)
  }

  const getCustomizeData = async () => {
    try {
      const res = await agent.common.getCustomizeModel(auth.currentUser.id)
      if (res.data.success) {
        dispatch(changePage('none'))
        dispatch(createCharacter(res.data))
        window.dragon.clickCustomize(res.data.data)
      }
    } catch (error) {
      if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
        dispatch(showToast('error', error.response.data.message))
        if (error.response.data.message == 'Unauthorized') {
          localStorage.removeItem('token')
          dispatch(logout())
          window.dragon.stopUnityGame()
          dispatch(changePage('login'))
        }
      }
    }
  }

  // const getNotificationData = async () => {
  //   try {
  //     const resNotificationtData = await agent.common.getNotification()
  //     if (resNotificationtData.data.success) {
  //       setNotificationData([...resNotificationtData.data.data])
  //       let count = 0;
  //       resNotificationtData.data.data.map((alarm) => {
  //         if (!alarm.read) {
  //           count++;
  //         }
  //       })
  //       setNotification(count)
  //     }
  //   } catch (error) {
  //     if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
  //       dispatch(showToast('error', error.response.data.message))
  //       if (error.response.data.message == 'Unauthorized') {
  //         localStorage.removeItem('token')
  //         dispatch(logout())
  //         window.dragon.stopUnityGame()
  //         dispatch(changePage('login'))
  //       }
  //     }
  //   }
  // }

  const handleClickNotification = () => {
    // getNotificationData()
    if(auth.currentUser.role === 'INTERPRETER' || auth.currentUser.role === 'SPEAKER') return
    toggleNotification()
  }

  const clickFullScreen = async (full) => {
    setShowFullScreenBtn(full)

    var videoGroup = document.getElementById('dragon-live-video')
    var fullVideo = document.getElementsByClassName('dragon-class')

    // console.log("fullvideo length=", fullVideo.length)

    if (fullVideo.length == 0) return
    let count = fullVideo.length

    let roleGroup = []
    let interpreterCount = 0;
    let roleId = -1;
    for(let k=0; k<count; k++) {
      let splitId = fullVideo[k].id.split('-')
      if(splitId[4] === 'cam') {
        roleGroup.push(auth.currentUser.role)
        if(auth.currentUser.role === 'INTERPRETER') {
          roleId = k
          interpreterCount++
        }
      } else {
        const res = await agent.auth.getDetailsUser(parseInt(splitId[5]))
        if (res.data.success) {
          if(res.data.data.role === 'INTERPRETER') {
            roleId = k
            interpreterCount++
          }
        }
      }
    }

    switch (count) {
      case 1:
        fullVideo[0].style.width = "85%"
        fullVideo[0].style.height = "80%"
        fullVideo[0].style.top = "0"
        fullVideo[0].style.left = "50%"
        fullVideo[0].style.transform = "translateX(-50%)"
        videoGroup.style.display = 'block'
        videoGroup.src = './assets/screens/video1.png'
        break
      case 2:
        if(roleId === 0) {
          fullVideo[0].style.width = "15%"
          fullVideo[0].style.height = "20%"
          fullVideo[0].style.bottom = "0"
          fullVideo[0].style.right = "0"

          fullVideo[1].style.width = "85%"
          fullVideo[1].style.height = "80%"
          fullVideo[1].style.top = "0"
          fullVideo[1].style.left = "50%"
          fullVideo[1].style.transform = "translateX(-50%)"
        } else if(roleId === 1) {
          fullVideo[1].style.width = "15%"
          fullVideo[1].style.height = "20%"
          fullVideo[1].style.bottom = "0"
          fullVideo[1].style.right = "0"

          fullVideo[0].style.width = "85%"
          fullVideo[0].style.height = "80%"
          fullVideo[0].style.top = "0"
          fullVideo[0].style.left = "50%"
          fullVideo[0].style.transform = "translateX(-50%)"
        } else {
          fullVideo[0].style.width = "50%"
          fullVideo[0].style.height = "80%"
          fullVideo[0].style.top = "0"
          fullVideo[0].style.left = "0"

          fullVideo[1].style.width = "50%"
          fullVideo[1].style.height = "80%"
          fullVideo[1].style.top = "0"
          fullVideo[1].style.left = "50%"
        }
        videoGroup.style.display = 'block'
        if(interpreterCount > 0) {
          videoGroup.src = './assets/screens/video2-1.png'
        } else {
          videoGroup.src = './assets/screens/video2.png'
        }
        break
      case 3:
        switch(roleId) {
          case 0:
            fullVideo[0].style.width = "15%"
            fullVideo[0].style.height = "20%"
            fullVideo[0].style.bottom = "0"
            fullVideo[0].style.right = "0"

            fullVideo[1].style.width = "50%"
            fullVideo[1].style.height = "80%"
            fullVideo[1].style.top = "0"
            fullVideo[1].style.left = "0"
            fullVideo[2].style.width = "50%"
            fullVideo[2].style.height = "80%"
            fullVideo[2].style.top = "0"
            fullVideo[2].style.left = "50%"
            break
          case 1:
            fullVideo[1].style.width = "15%"
            fullVideo[1].style.height = "20%"
            fullVideo[1].style.bottom = "0"
            fullVideo[1].style.right = "0"

            fullVideo[0].style.width = "50%"
            fullVideo[0].style.height = "80%"
            fullVideo[0].style.top = "0"
            fullVideo[0].style.left = "0"
            fullVideo[2].style.width = "50%"
            fullVideo[2].style.height = "80%"
            fullVideo[2].style.top = "0"
            fullVideo[2].style.left = "50%"
            break
          case 2:
            fullVideo[2].style.width = "15%"
            fullVideo[2].style.height = "20%"
            fullVideo[2].style.bottom = "0"
            fullVideo[2].style.right = "0"

            fullVideo[0].style.width = "50%"
            fullVideo[0].style.height = "80%"
            fullVideo[0].style.top = "0"
            fullVideo[0].style.left = "0"
            fullVideo[1].style.width = "50%"
            fullVideo[1].style.height = "80%"
            fullVideo[1].style.top = "0"
            fullVideo[1].style.left = "50%"
            break
          default:
            fullVideo[0].style.width = "33.333333333333%"
            fullVideo[0].style.height = "80%"
            fullVideo[0].style.top = "0"
            fullVideo[0].style.left = "0"

            fullVideo[1].style.width = "33.333333333333%"
            fullVideo[1].style.height = "80%"
            fullVideo[1].style.top = "0"
            fullVideo[1].style.left = "33.333333333333%"

            fullVideo[2].style.width = "33.333333333333%"
            fullVideo[2].style.height = "80%"
            fullVideo[2].style.top = "0"
            fullVideo[2].style.left = "66.66666666666%"
            break
        }
        videoGroup.style.display = 'block'
        if(interpreterCount > 0) {
          videoGroup.src = './assets/screens/video3-1.png'
        } else {
          videoGroup.src = './assets/screens/video1.png'
        }
        break
    }

    if (count == 0) {
      document.getElementById("dragon-video-group").style.height = "0px"
    } else {
      document.getElementById("dragon-video-group").style.height = "100vh"
    }

    if (full) {
      // console.log("show full video")
      document.getElementById("unity-canvas").style.display = "none"
      for (let i = 0; i < fullVideo.length; i++) {
        fullVideo[i].style.display = 'block'
      }
    } else {
      // console.log("hide full video")
      document.getElementById("unity-canvas").style.display = "block"
      videoGroup.style.display = 'none'
      for (let k = 0; k < fullVideo.length; k++) {
        fullVideo[k].style.display = 'none'
      }
    }
  }

  const exitClick = () => {
    document.getElementById("unity-canvas").style.display = "block"
    var videoGroup = document.getElementById('dragon-live-video')
    videoGroup.style.display = 'none'
    var fullVideo = document.getElementsByClassName('dragon-class')
    for (let k = 0; k < fullVideo.length; k++) {
      fullVideo[k].style.display = 'none'
    }
    window.dragon.outLectureRoom()
    setActiveSound(true)
    setActiveMic(true)
    setLockCamera(true)
  }

  const lockClick = (isLock) => {
    setLockCamera(isLock)
    if (isLock) {
      window.dragon.lockCamera(1)
    } else {
      window.dragon.lockCamera(0)
    }
  }

  const goOutPlay = (page) => {
    if((page === 'progress' && (auth.currentUser.role === 'PRODUCER' || auth.currentUser.role === 'INTERPRETER' || auth.currentUser.role === 'SPEAKER')) || 
    (page === 'ranking' && (auth.currentUser.role === 'PRODUCER' || auth.currentUser.role === 'INTERPRETER' || auth.currentUser.role === 'SPEAKER')) ||
    (page === 'schedule' && (auth.currentUser.role === 'INTERPRETER' || auth.currentUser.role === 'SPEAKER'))) return
    dispatch(changePage(page))
    window.dragon.clickHome()
    window.dragon.outLectureRoom()
    window.dragon.reactKeyboard()
    setShowBtn(false)
  }

  const micActive = (act) => {
    setActiveMic(act)
    window.dragon.micActive(act)
  }

  const soundActive = (act) => {
    setActiveSound(act)
    window.dragon.soundActive(act)
  }

  useEffect(() => {
    const getData = async () => {
      try {
        const res = await agent.common.getMapData(auth.currentUser.id)
        if (res.data.success) {
          setMapData([...res.data.data])
        }
      } catch (error) {
        if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    getData()

    let modelData = auth.characterData
    setAvatarImg(localStorage.getItem('avatar3_' + modelData.gendor + modelData.hair + modelData.face + modelData.clothes + modelData.shoes + modelData.skin))

    if (common.isLive) {
      setLockCamera(true)
      window.dragon.makeCharacter(auth.characterData)
      window.dragon.clickPlay(auth.currentUser.role, common.nextEvent, auth.currentUser.id)
      window.dragon.adminLecture(auth.currentUser.role)
      dispatch(isLive(false))
    }
  }, [])

  useEffect(() => {
    const getUserData = async () => {
      try {
        const resNotificationData = await agent.common.getNotification()
        if (resNotificationData.data.success) {
          let notificationArray = []
          resNotificationData.data.data.map((notUser) => {
            notificationArray.push({
              id: notUser.id,
              title: notUser.title,
              description: notUser.message,
              type: notUser.type,
              link: "",
              viewed: notUser.read,
            })
          })

          setNotificationData([...notificationArray])
          let count = 0;
          notificationArray.map((alarm) => {
            if (!alarm.viewed) {
              count++;
            }
          })
          setNotification(count)
        }
      } catch (error) {
        if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
          // clearInterval(intervalID);
          // console.log("interval id: ", intervalID)
          // intervalID = -1
        }
      }
    }

    const getAdminData = async () => {
      try {
        const resNotificationData = await agent.common.notification()
        const resNotificationUserData = await agent.common.notificationUser()
        if (resNotificationData.data.success && resNotificationUserData.data.success) {
          let notificationArray = []
          resNotificationUserData.data.data.map((notUser) => {
            if (notUser.id_user === auth.currentUser.id) {
              for (let i = 0; i < resNotificationData.data.data.length; i++) {
                if (notUser.id_notification === resNotificationData.data.data[i].id) {
                  let type
                  switch (resNotificationData.data.data[i].type) {
                    case "LESSON":
                      type = 1
                      break
                    case "MEETING":
                      type = 2
                      break
                    case "INTERACTION":
                      type = 3
                      break
                  }
                  notificationArray.push({
                    id: notUser.id_notification,
                    title: resNotificationData.data.data[i].title,
                    description: resNotificationData.data.data[i].description,
                    type: type,
                    link: resNotificationData.data.data[i].link,
                    viewed: notUser.viewed,
                  })
                }
              }
            }
          })

          setNotificationData([...notificationArray])
          let count = 0;
          notificationArray.map((alarm) => {
            if (!alarm.viewed) {
              count++;
            }
          })
          setNotification(count)
        }
      } catch (error) {
        if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
          // clearInterval(intervalID);
          // console.log("interval id: ", intervalID)
          // intervalID = -1
        }
      }
    }

    if(auth.currentUser.role !== 'INTERPRETER' && auth.currentUser.role !== 'SPEAKER') {
      if (auth.currentUser.role === 'ADMIN') {
        getAdminData()
      } else {
        getUserData()
      }
    }
    

    // intervalID = setInterval(function () {
    // 	getData()
    // 	clearInterval(intervalID);
    // }, 10000);
  }, [])

  window.dragon.saveFavorite = function (data) {
    const setData = async (data) => {
      const res = await agent.common.setMapData(
        auth.currentUser.id,
        data
      )
      if (res.data.success) dispatch(showToast('success', res.data.message))
      else dispatch(showToast('error', res.data.message))
    }
    console.log("setData", data)
    setData(data)
  }

  window.dragon.showMoreBtn = function (isShow) {
    setShowBtn(isShow)
  }

  window.dragon.getUserName = function (id) {
    const getData = async (id) => {
      const res = await agent.auth.getDetailsUser(id)
      if (res.data.success) {
        window.dragon.sendUserNameAndRole(res.data.data.firstname + ' ' + res.data.data.lastname + '|' + res.data.data.role + '|' + id)
      }
    }
    getData(id)
  }

  return (
    <div className={styles.main}>
      <div className={styles.home}>
        <div className={styles.world}>
          <div className={styles.left_border_img_container}>
            <img className={styles.left_border_img} src={getImg('image/back/small_sidemenu_back.png')} alt="Logo" />
          </div>
          <div className={styles.world_logo_control_container}>
            <div className={styles.world_logo_container} onClick={() => getCustomizeData()}>
              {
                avatarImg === '' ?
                  <img className={styles.left_border_img} src={getImg('image/back/avatar.png')} alt="Logo" />
                  :
                  <img className={styles.left_border_img} src={"data:image/png;base64," + avatarImg} alt="Logo" />
              }
            </div>
            <div className={styles.world_logo_text} onClick={() => goOutPlay('home')}>
              Voltar
            </div>
          </div>
          <div className={styles.world_header_btn_container}>
            <div className={`${styles.button} ${styles.world_header_small_btn}`} onClick={() => goOutPlay('progress')}>
              <img className={styles.world_btn} src={getImg('image/icons/playbtn1.png')} alt="icons" />
            </div>
            <div className={`${styles.button} ${styles.world_header_medium_btn}`} onClick={() => goOutPlay('ranking')}>
              <img className={styles.world_btn} src={getImg('image/icons/playbtn2.png')} alt="icons" />
            </div>
            <div className={`${styles.button} ${styles.world_header_large_btn}`} onClick={() => handleClick()}>
              <img className={styles.world_btn} src={getImg('image/icons/playbtn3.png')} alt="icons" />
            </div>
            <div className={`${styles.button} ${styles.world_header_medium_btn}`} onClick={() => goOutPlay('schedule')}>
              <img className={styles.world_btn} src={getImg('image/icons/playbtn4.png')} alt="icons" />
            </div>
            <div className={`${styles.world_header_small_btn} ${styles.notification_btn}`}>
              <div className={`${styles.click_btn} ${styles.button}`} onClick={() => handleClickNotification()}>
                <Badge className={styles.badge} badgeContent={notification}>
                  <img className={styles.world_btn} src={getImg('image/icons/playbtn5.png')} alt="icons" />
                </Badge>
              </div>
              <div className={styles.notification_container}>
                <div className={styles.rel_container}>
                  {
                    showNotification && <Notification alarms={notificationData} toggleNotification={toggleNotification} />
                  }
                </div>
              </div>
            </div>
          </div>
          {
            !showBtn &&
              <div className={styles.tutorial_btn_container}>
                <div className={styles.control_btn_container} onClick={() => setShowControlModal(true)}>
                  <div className={styles.control_p}>
                    P
                  </div>
                  <div className={styles.control_text}>
                    Controles
                  </div>
                </div>
                <div className={styles.tip_btn_container} onClick={() => setShowTipModal(true)}>
                  <div className={styles.tip_r}>
                    R
                  </div>
                  <div className={styles.tip_text}>
                    Dicas
                  </div>
                </div>
                <div className={styles.tip_btn_container} onClick={() => setShowTipModal(true)}>
                  <div className={styles.tip_r}>
                    R
                  </div>
                  <div className={styles.tip_text}>
                    Dicas
                  </div>
                </div>
              </div>
          }
          

          {
            detailMiddleBtn ?
            <div className={styles.detail_middle_btn_container} onMouseLeave={() => setDetailMiddleBtn(false)}>
              <div className={styles.rel_container}>
                <div className={styles.middle0_btn}>
                  <img src={getImg('image/icons/middle_btn_0.png')} alt="icons" />
                </div>
                <div className={styles.middle1_btn}>
                  <img src={getImg('image/icons/middle_btn_1.png')} alt="icons" />
                </div>
                <div className={styles.middle2_btn}>
                  <img src={getImg('image/icons/middle_btn_2.png')} alt="icons" />
                </div>
                <div className={styles.middle3_btn}>
                  <img src={getImg('image/icons/middle_btn_3.png')} alt="icons" />
                </div>
                <div className={styles.middle4_btn}>
                  <img src={getImg('image/icons/middle_btn_4.png')} alt="icons" />
                </div>
              </div>  
            </div> 
            :
            <div className={styles.middle_btn_container} onMouseOver={() => setDetailMiddleBtn(true)}>
              <img src={getImg('image/icons/middle_btn_0.png')} alt="icons" />
            </div>
          }

          {
            showBtn && 
            <div className={`${styles.world_footer_btn_container} ${showFullScreenBtn && styles.fixed_pos}`}>
              {
                activeSound ?
                  <div className={`${styles.button} ${styles.world_header_small_btn}`} onClick={() => soundActive(false)} title={`Desligar o som`}>
                    <div className={styles.btn_text}>
                      <VolumeUpIcon sx={{fontSize: 40}} />
                    </div>
                  </div>
                  :
                  <div className={`${styles.button} ${styles.world_header_small_btn}`} onClick={() => soundActive(true)} title={`Desligar o som`}>
                    <div className={styles.btn_text}>
                      <VolumeMuteIcon sx={{fontSize: 40}} />
                    </div>
                  </div>
              }
              {
                auth.currentUser.role !== "USER" &&
                <>
                  {
                    aciveMic ?
                    <div className={`${styles.button} ${styles.world_header_small_btn}`} onClick={() =>micActive(false)} title={`Desligar microfone`}>
                      <div className={styles.btn_text}>
                        <MicIcon sx={{fontSize: 40}} />
                      </div>
                    </div>
                    :
                    <div className={`${styles.button} ${styles.world_header_small_btn}`} onClick={() => micActive(true)} title={`Desligar microfone`}>
                      <div className={styles.btn_text}>
                        <MicOffIcon sx={{fontSize: 40}} />
                      </div>
                    </div>
                  }
                </>
              }
              
              <div className={`${styles.button} ${styles.world_header_small_btn} ${styles.lock}`} onClick={() => exitClick()} title={`Sair da palestra`}>
                <div className={styles.btn_text}>
                  <FontAwesomeIcon icon={faDoorOpen} />
                </div>
              </div>
              {
                lockCamera ?
                  <div className={`${styles.button} ${styles.world_header_small_btn} ${styles.lock}`} onClick={() => lockClick(false)} title={`Bloquear movimento da tela`}>
                    <div className={styles.btn_text}>
                      <FontAwesomeIcon icon={faLock} />
                    </div>
                  </div>
                  :
                  <div className={`${styles.button} ${styles.world_header_small_btn} ${styles.lock}`} onClick={() => lockClick(true)} title={`Bloquear movimento da tela`}>
                    <div className={styles.btn_text}>
                      <FontAwesomeIcon icon={faUnlock} />
                    </div>
                  </div>
              }
              {
                !showFullScreenBtn ?
                  <div className={`${styles.button} ${styles.world_header_small_btn}`} onClick={() => clickFullScreen(true)} title={`Expandir tela`}>
                    <div className={styles.btn_text}>
                      <FontAwesomeIcon icon={faExpand} />
                    </div>
                  </div>
                  :
                  <div className={`${styles.button} ${styles.world_header_small_btn}`} onClick={() => clickFullScreen(false)} title={`Expandir tela`}>
                    <div className={styles.btn_text}>
                      <FontAwesomeIcon icon={faCompress} />
                    </div>
                  </div>
              }
            </div>
          }
        </div>
        {
          showTipModal && <Tip setShowTipModal={setShowTipModal}/>
        }
        {
          showControlModal && <Controls setShowControlModal={setShowControlModal}/>
        }
      </div>
    </div>
  )
}

export default Play